* {
    font-family: "Roboto";
    margin: 0;
    box-sizing: border-box;
}


header {
    position: fixed;
    height: 100px;
    top: 0;
    background: #ffd800;
    width: 100%;
    text-align: center;
    padding: 3em;
    padding-bottom: 6em;
    z-index: 10;
}

h1 {
    font-family: "Roboto";
    font-weight: bold;
    text-rendering: optimizeLegibility;
}

h1 span {
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #000000;
}

#inputSection {
    width: 70%;
    text-align: center;
    padding: 5em;
    margin: 0 auto;
    margin-top: 100px;
}

#entryFields {
    margin-left: -300px;
}

#inputSection p {
    display: inline;
    padding: 10px;

}

#inputSection label {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background: none;
    color: #262626;
    font-size: 1rem;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: .1em;
    line-height: 1.3;
}

#inputSection input {
    padding: 10px;
    margin: 10px;
    width: 60%;
    height: 40px;
    background: none;
    color: #262626;
    font-size: 1rem;
    text-decoration: none;
    line-height: 1.3;
    border: 2px solid #262626;
    border-radius: 3px;
    outline: 0;
    opacity: 1;
}

#inputSection button {
    display: inline-block;
    background: none;
    color: #262626;
    font-size: 1rem;
    font-weight: 700;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: .1em;
    line-height: 1.3;
    margin: 2em .4em .6em 0;
    padding: .8835em 2.7em;
    cursor: pointer;
    border: 2px solid #262626;
    border-radius: 3px;
    outline: 0;
    opacity: 1;
}

#htmlOutputLines {
    width: 90%;
    max-width: 1000px;
    margin: 0 auto;
    background: none;
    color: #262626;
    font-size: 1rem;
    text-decoration: none;
    line-height: 2;
    padding: .8835em 2.7em;
    border: 2px solid #262626;
    border-radius: 3px;
    position: relative;
}

#htmlOutputLines li {
    width: 90%;
}

#htmlOutputLines button {
    position: absolute;
    right: 40px;
    display: inline-block;
    background: none;
    color: #262626;
    font-size: 1rem;
    font-weight: 700;
    text-align: center;
    text-decoration: none;
    letter-spacing: .1em;
    line-height: 1.3;
    cursor: pointer;
    border: 2px solid #262626;
    border-radius: 3px;
    outline: 0;
    opacity: 1;
}

#htmlOutput {
    width: 90%;
    max-width: 1000px;
    margin: 10px auto;
    background: none;
    color: #262626;
    font-size: 1rem;
    text-decoration: none;
    line-height: 1.3;
    padding: .8835em 2.7em;
    border: 2px solid #262626;
    border-radius: 3px;
}

#formattedOutput {
    width: 90%;
    max-width: 1000px;
    margin: 0 auto;
    background: none;
    color: #262626;
    font-size: 1rem;
    text-decoration: none;
    line-height: 1.3;
    padding: .8835em 2.7em;
    border: 2px solid #262626;
    border-radius: 3px;
    margin-bottom: 100px;
}


footer {
    position: fixed;
    bottom: 0;
    background: #ffd800;
    width: 100%;
    text-align: center;
    height: 50px;
    padding-top: 15px;
}